import { GameMechanicState } from "./game-mechanics";

class EnergyMilestoneState extends GameMechanicState {
  get requirement() {
    return this.config.requirement;
  }

  get isReached() {
    return Currency.energy.gte(this.requirement);
  }

  get isEffectActive() {
    return this.isReached;
  }
}

export const EnergyMilestone = mapGameDataToObject(
  GameDatabase.energyMilestones,
  config => new EnergyMilestoneState(config)
);