import { ADNotations } from "./ad-notations";

const notation = new ADNotations.MixedScientificNotation();

const commaRegexp = /\B(?=(\d{3})+(?!\d))/gu;
function formatWithCommas(value) {
  const decimalPointSplit = value.toString().split(".");
  decimalPointSplit[0] = decimalPointSplit[0].replace(commaRegexp, ",");
  return decimalPointSplit.join(".");
}

window.format = function format(value, places, placesUnder1000) {
  if (player.formatEND) return "END";
  return notation.format(value, places, placesUnder1000, 3);
}

window.formatInt = function formatInt(value) {
  if (player.formatEND) return "END";
  if (typeof value === "number" && value <= 1e9) {
    return formatWithCommas(value.toFixed(0));
  }
  return format(value);
}

window.formatX = function formatX(value, places, placesUnder1000) {
  return `${format(value, places, placesUnder1000)}`;
}

window.formatPow = function formatPow(value, places, placesUnder1000) {
  return `^${format(value, places, placesUnder1000)}`;
}

window.formatPercents = function formatPow(value, places, placesUnder1000) {
  return `${format(value * 100, places, placesUnder1000)}%`;
}