import { DC } from "./constants";

export const FirstReset = {
  get gainedEnergy() {
    return player.maxSingularities.log10().div(3).pow(1.5).floor();
  },
  
  energyToSingularities(energy) {
    return energy.root(1.5).times(3).pow10();
  },
  
  get canReset() {
    return this.gainedEnergy.gt(player.energy);
  },
  
  reset() {
    if (!this.canReset) return;
    Currency.energy.value = Currency.energy.value.max(this.gainedEnergy);
    Currency.singularities.reset();
    player.maxSingularities = DC.D1;
    Condenser.amount = DC.D0;
    Amplifier.amount = DC.D0;
  },
  
  get hasReached() {
    return Currency.energy.gt(0) || Galaxy.amount.gt(0);
  }
}