<script>
import CondenserButton from "./CondenserButton";
import AmplifierButton from "./AmplifierButton";
import MessageModal from "./MessageModal";
import SaveInfo from "./SaveInfo";
import FirstResetButton from "./FirstResetButton";
import EnergyMilestoneComponent from "./EnergyMilestoneComponent";
import GalaxyContainer from "./GalaxyContainer";

export default {
  name: "MainContent",
  components: {
    CondenserButton,
    AmplifierButton,
    MessageModal,
    SaveInfo,
    FirstResetButton,
    EnergyMilestoneComponent,
    GalaxyContainer
  },
  data() {
    return {
      singularities: new Decimal(),
      energy: new Decimal(),
      firstResetUnlocked: false,
      galaxyUnlocked: false
    }
  },
  computed: {
    energyMilestones() {
      const milestones = [];
      const all = EnergyMilestone.all;
      for (let i = 0; i < all.length; i += 2) {
        milestones.push([all[i], all[i + 1]]);
      }
      return milestones;
    }
  },
  methods: {
    update() {
      this.singularities.copyFrom(Currency.singularities);
      this.energy.copyFrom(Currency.energy);
      this.firstResetUnlocked = FirstReset.hasReached;
      this.galaxyUnlocked = Galaxy.isUnlocked;
    }
  }
}
</script>

<template>
  <div class="l-main-content">
    <MessageModal />
    <SaveInfo />
    <div class="l-game-header-resources">
      <div v-if="firstResetUnlocked"> <span class="c-energy-amount">{{ format(energy, 2) }}</span> </div>
      <div> <span class="c-singularity-amount">{{ format(singularities, 2, 1) }}</span> </div>
    </div>
    <div class="l-main-upgrade-row">
      <CondenserButton />
      <div class="o-row-space" />
      <AmplifierButton />
    </div>
    <GalaxyContainer v-if="galaxyUnlocked" />
    <div class="o-line" />
    <FirstResetButton />
    <div
      class="c-energy-milestone-container"
      v-if="firstResetUnlocked"
    >
      <div class="c-energy-milestone-title"></div>
      <div
        v-for="(row, index) in energyMilestones"
        :key="index + 'row-energy-milstone'"
        class="l-energy-milestone-row"
      >
        <EnergyMilestoneComponent :milestone="row[0]" />
        <div class="o-row-space" />
        <EnergyMilestoneComponent :milestone="row[1]" />
      </div>
    </div>
  </div>
</template>

<style scope>
.o-line {
  width: 100%;
  height: 2px !important;
  background-color: #e3e3e3;
  margin: 17.5px 0;
}

.c-energy-milestone-container {
  margin-top: 10px;
}

.c-energy-milestone-title {
  font-weight: bold;
  font-size: 18px;
  color: var(--color-galaxy);
}
</style>