import { DC } from "./constants";

export const Galaxy = {
  get amount() {
    return player.galaxies;
  },

  set amount(value) {
    player.galaxies = value;
  },

  get isUnlocked() {
    return EnergyMilestone.unlockGalaxy.canBeApplied || this.amount.gt(0);
  },

  get effect() {
    return DC.D2.add(this.amount.timesEffectOf(EnergyMilestone.galaxyBoost));
  },

  get cost() {
    return DC.D2.times(DC.D5.pow(this.amount)).timesEffectOf(
      EnergyMilestone.galaxyCheaper
    ).round();
  },

  get isAffordable() {
    return Currency.energy.gte(this.cost);
  },

  purchase() {
    if (!this.isAffordable) return;
    FirstReset.reset();
    Currency.energy.subtract(this.cost);
    this.amount = this.amount.add(1);
  },

  buyMax() {
    if (!this.isAffordable) return;
    const scaling = new LinearCostScaling(
      Currency.energy.value,
      this.cost,
      DC.D5
    );
    if (scaling.purchases.lte(0)) return;
    FirstReset.reset();
    Currency.energy.subtract(scaling.totalCost);
    this.amount = this.amount.add(scaling.purchases);
  }
}