import { DC } from "./constants";

class CondenserState {
  get amount() {
    return player.condensers;
  }
  
  set amount(value) {
    player.condensers = value;
  }

  get productionPerSecond() {
    let multiplier = this.amount.pow(Galaxy.effect);
    multiplier = multiplier.times(Amplifier.multiplier);
    multiplier = multiplier.timesEffectsOf(
      EnergyMilestone.multFromSingulatities,
      EnergyMilestone.energyBoost
    );
    return multiplier;
  }

  /* Seconds */
  tick(diff) {
    Currency.singularities.add(this.productionPerSecond.times(diff));
  }
  
  get cost() {
    return DC.D5.pow(this.amount);
  }
  
  get isAffordable() {
    return Currency.singularities.gte(this.cost);
  }
  
  purchase() {
    if (!this.isAffordable) return;
    Currency.singularities.subtract(this.cost);
    this.amount = this.amount.add(1);
  }
  
  buyMax() {
    if (!this.isAffordable) return;
    const scaling = new LinearCostScaling(
      Currency.singularities.value,
      this.cost,
      DC.D5
    );
    if (scaling.purchases.lte(0)) return;
    
    Currency.singularities.subtract(scaling.totalCost);
    this.amount = this.amount.add(scaling.purchases);
  }
}

export const Condenser = new CondenserState();