<script>
import DescriptionDisplay from "./DescriptionDisplay";
import EffectDisplay from "./EffectDisplay";

export default {
  name: "EnergyMilestoneComponent",
  props: {
    milestone: {
      type: Object,
      required: true
    }
  },
  components: {
    DescriptionDisplay,
    EffectDisplay
  },
  data() {
    return {
      isReached: false
    }
  },
  computed: {
    config() {
      return this.milestone.config;
    },
    requirement() {
      return this.config.requirement;
    },
    unlockClass() {
      return this.isReached ? "" : "c-energy-milestone--locked";
    }
  },
  methods: {
    update() {
      const milestone = this.milestone;
      this.isReached = milestone.isReached;
    }
  }
}
</script>

<template>
  <div
    class="c-energy-milstone"
    :class="unlockClass"
  >
    <DescriptionDisplay :config="config" />
    <EffectDisplay :config="config" />
    <span>: {{ format(requirement, 2) }} </span>
  </div>
</template>

<style scope>
</style>