<script>
import PrimaryButton from "./PrimaryButton";

export default {
  name: "FirstResetButton",
  components: {
    PrimaryButton
  },
  data() {
    return {
      canReset: false,
      gainedEnergy: new Decimal(),
      nextAt: new Decimal()
    }
  },
  computed: {
    text() {
      if (this.canReset) {
        return ` ${format(this.gainedEnergy, 2)} 
          ( ${format(this.nextAt, 2, 2)} )`;
      }
      return ` ${format(this.nextAt, 2, 2)} `;
    }
  },
  methods: {
    update() {
      this.canReset = FirstReset.canReset;
      if (this.canReset) {
        this.gainedEnergy = FirstReset.gainedEnergy.minus(player.energy);
        this.nextAt = FirstReset.energyToSingularities(FirstReset.gainedEnergy.add(1));
      } else {
        this.nextAt = FirstReset.energyToSingularities(player.energy.add(1));
      }
    },
    reset() {
      FirstReset.reset();
    }
  }
}
</script>

<template>
  <PrimaryButton
    class="c-first-reset"
    @click="reset"
    :enabled="canReset"
  >
    {{ text }}
  </PrimaryButton>
</template>

