<script>
import PrimaryButton from "./PrimaryButton";

export default {
  name: "CondenserButton",
  components: {
    PrimaryButton
  },
  data() {
    return {
      effect: new Decimal(),
      cost: new Decimal(),
      isAffordable: false
    }
  },
  methods: {
    update() {
      this.effect = Condenser.productionPerSecond;
      this.cost = Condenser.cost;
      this.isAffordable = Condenser.isAffordable;
    },
    purchase() {
      if (EnergyMilestone.qols.canBeApplied) {
        Condenser.buyMax();
      } else {
        Condenser.purchase();
      }
    }
  }
}
</script>

<template>
  <PrimaryButton
    @click="purchase"
    :enabled="isAffordable"
    class="o-main-upgrade-btn"
  >
    <span></span>
    <br>
    <span>: +{{ format(effect, 2) }} /</span>
    <br>
    <span>: {{ format(cost, 2) }} </span>
  </PrimaryButton>
</template>

