import { DC } from "./constants";

class AmplifierState {
  get amount() {
    return player.amplifiers;
  }
  
  set amount(value) {
    player.amplifiers = value;
  }

  get multiplier() {
    let multiplier = EnergyMilestone.improveAmplifier2.canBeApplied
      ? DC.D2.pow(this.amount)
      : this.amount.add(1);
    multiplier = multiplier.timesEffectOf(EnergyMilestone.improveAmplifier);
    return multiplier;
  }
  
  get cost() {
    return DC.E1.pow(this.amount).times(DC.E2);
  }
  
  get isAffordable() {
    return Currency.singularities.gte(this.cost);
  }
  
  purchase() {
    if (!this.isAffordable) return;
    Currency.singularities.subtract(this.cost);
    this.amount = this.amount.add(1);
  }
  
  buyMax() {
    if (!this.isAffordable) return;
    const scaling = new LinearCostScaling(
      Currency.singularities.value,
      this.cost,
      DC.E1
    );
    
    if (scaling.purchases.lte(0)) return;
    
    Currency.singularities.subtract(scaling.totalCost);
    this.amount = this.amount.add(scaling.purchases);
  }
}

export const Amplifier = new AmplifierState();