import { DEV } from "@/env";
import { deepmergeAll } from "./deepmerge";

export const GameStorage = {
  _timeSinceLastSave: 0,
  saveInterval: 2e4,

  // UI only
  hasSaved: false,
  
  get timeSinceLastSave() {
    return this._timeSinceLastSave;
  },
  
  set timeSinceLastSave(value) {
    this._timeSinceLastSave = value;
    if (this._timeSinceLastSave >= this.saveInterval) {
      this.save();
    }
  },

  get saveKey() {
    return DEV ? "demo-dev-save" : "demo-save";
  },

  init() {
    const localSave = localStorage.getItem(this.saveKey);
    if (localSave === null) return;
    const playerObject = JSON.parse(localSave);
    player = deepmergeAll([Player.defaultStart, playerObject]);
  },
  
  save() {
    localStorage.setItem(this.saveKey, JSON.stringify(player));
    this._timeSinceLastSave = 0;
    this.hasSaved = true;
  },
  
  hardReset() {
    player = deepmergeAll([{}, Player.defaultStart]);
    this.save();
  }
}