<script>
import PrimaryButton from "./PrimaryButton";

export default {
  name: "GalaxyContainer",
  components: {
    PrimaryButton
  },
  data() {
    return {
      galaxies: new Decimal(),
      effect: new Decimal(),
      isAffordable: false,
      cost: new Decimal(),
      canReset: false
    }
  },
  computed: {
    unlockClass() {
      return this.isAffordable ? "c-buy-galaxy-button--affordable" : "";
    }
  },
  methods: {
    update() {
      this.galaxies.copyFrom(Galaxy.amount);
      this.effect = Galaxy.effect;
      this.isAffordable = Galaxy.isAffordable;
      this.cost = Galaxy.cost;
      this.canReset = FirstReset.canReset;
    },
    purchase() {
      Galaxy.purchase();
    },
    buyMax() {
      Galaxy.buyMax();
    }
  }
}
</script>

<template>
  <div>
    <div class="c-galaxy-title"></div>
    <div class="l-galaxy-row">
      <div class="c-galaxy-info">
        <div><span class="c-energy-amount">{{ format(galaxies, 2) }}</span></div>
        <div> {{ formatPow(2) }}  {{ formatPow(effect, 2) }}</div>
      </div>
      <div class="o-row-space" />
      <div class="c-galaxy-buttons">
        <PrimaryButton
          class="c-buy-galaxy-button"
          :class="unlockClass"
          :enabled="isAffordable"
          @click="purchase"
        >
          <span v-if="canReset"></span>
          <br>
          {{ format(cost, 2) }} 
        </PrimaryButton>
        <div class="o-column-space" />
        <PrimaryButton
          class="c-buy-galaxy-button"
          :class="unlockClass"
          :enabled="isAffordable"
          @click="buyMax"
        >
          
        </PrimaryButton>
      </div>
    </div>
  </div>
</template>

